.App {
  text-align: center;
  margin: 0;
  padding: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.OverHeader{
  background-color: #353535;
  height: 7vh;
  width: 100vw;
  text-align: center;
  color: goldenrod;
  margin: 0;
  padding: 0;
  display: flex;
  align-content: center;
  justify-content: center;
}

.Home{
  margin-top: 0vh;
  text-align: center;
  align-content: center;
  justify-content: center;

  display: flex;
}
